  color: grey;
}
.v-text-field input {
  height: 100% !important
}
.v-input input {
  max-height: 100% !important;
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease
}

.v-enter-from,
.v-leave-to {
  opacity: 0
}
</style>
